import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,
         Alert, AlertController, Loading, LoadingController } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthProvider } from '../../providers/auth/auth';
import { EmailValidator } from '../../validators/email';
import { HomePage } from '../home/home';
import { SitelistPage } from '../sitelist/sitelist';

/**
 * Generated class for the SignupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {

  public signupForm:FormGroup;
  public loading:Loading;

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public alertCtrl:AlertController,
              public loadingCtrl:LoadingController,
              public authProvider:AuthProvider,
              formBuilder:FormBuilder) {
              
              this.signupForm = formBuilder.group({
                email:[
                  '', Validators.compose([Validators.required, EmailValidator.isValid])
                ],
                password:[
                  '', Validators.compose([Validators.minLength(6), Validators.required])
                ],
                firstName:[
                  '', Validators.compose([Validators.minLength(1), Validators.required])
                ],
                lastName:[
                  '', Validators.compose([Validators.minLength(1), Validators.required])
                ]
                

              });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignupPage');
  }

  signupUser():void{
    if(!this.signupForm.valid){
      console.log(
        `Need to complete the form, current value: ${this.signupForm.value}`
      );
    }else{
      const email: string = this.signupForm.value.email;
      const password: string = this.signupForm.value.password;
      const firstName: string = this.signupForm.value.firstName;
      const lastName: string = this.signupForm.value.lastName;

      this.authProvider.signupUser(email,password, firstName, lastName).then(
        user => {
          this.loading.dismiss().then(() => {
            this.navCtrl.setRoot(SitelistPage);
          });
        },
        error => {
          this.loading.dismiss().then(() => {
            const alert: Alert = this.alertCtrl.create({
              message:error.message,
              buttons: [{text:'Ok', role: 'cancel'}]
            });
            alert.present();
          });
        }
      );
      this.loading = this.loadingCtrl.create();
      this.loading.present();
    }
  }

}



